.timer-body {
  background-image: linear-gradient(to right, #00f260, #0575e6);
  background-size: 200%;
  background-attachment: fixed;
  animation: bg-animation 5s infinite alternate;
}

@keyframes bg-animation {
  0% {
    background-position: 0;
  }

  100% {
    background-position: 100%;
  }
}

.general-wrapper {
  text-align: center;
}

.input {
  padding: 15px;
  font-size: 25px;
}

.button {
  color: wheat;
  cursor: pointer;
  background: indigo;
  border: 0;
  border-radius: 20px;
  margin: 5px;
  padding: 15px;
  font-size: 24px;
}

.button:hover, .button:focus {
  background-color: #000;
  transform: scale(1.1);
}

.timer {
  justify-content: center;
  gap: 50px;
  font-size: 35px;
  display: flex;
}

.field {
  flex-direction: column;
  display: flex;
}

.label {
  font-weight: 900;
}

